//iscroll _this,up.down 
function fetchMore(_this,up,down){
  let pullDownFlag,pullUpFlag,timer;
  _this.myscroll.on('scroll', positionJudge);
  _this.myscroll.on('scrollEnd', action);
  function positionJudge() {
    //
    if (this.y > 20) {
      //console.log( "");
      pullDownFlag = 1;
      clearTimeout(timer);
      //
    } else if (this.y < (this.maxScrollY - 20)) {
      //console.log( "");
      pullUpFlag = 1;
      clearTimeout(timer);
    }
  }
  function action(){
    //,scrollend
    timer = setTimeout(function(){
      if(pullDownFlag==1){
        //pullDownAction();
        if(down){
          _this.page =0;
          down(_this);
        }
        console.log('');
        pullDownFlag = 0;

      }else if(pullUpFlag==1){
        //pullUpAction();
        if(up){
          up(_this);
        }
        console.log('');
        pullUpFlag = 0;
      }
    },200);
  }
}
export default {fetchMore};



